module.exports = "\n\n<div _v-73b20894=\"\">\n  <section class=\"markdown\" _v-73b20894=\"\">\n    <h1 _v-73b20894=\"\">Iconfont</h1>\n    <p _v-73b20894=\"\">\n      语义化的矢量图形。\n    </p>\n    <h2 _v-73b20894=\"\">图标的命名规范</h2>\n    <p _v-73b20894=\"\">\n      我们为每个图标赋予了语义化的命名，命名规则如下:\n    </p>\n    <ul _v-73b20894=\"\">\n      <li _v-73b20894=\"\">\n        实心和描线图标保持同名，用 -o 来区分，比如 question-circle(实心) 和 question-circle-o(描线)；\n      </li>\n      <li _v-73b20894=\"\">\n        命名顺序：[icon名]-[形状可选]-[描线与否]-[方向可选]。\n      </li>\n    </ul>\n\n    <h2 _v-73b20894=\"\">如何使用</h2>\n    <p _v-73b20894=\"\">使用 <v-icon _v-73b20894=\"\"></v-icon>标签声明组件，指定图标对应的 type 属性，示例代码如下:</p>\n    <pre _v-73b20894=\"\"><code class=\"lang-html\" _v-73b20894=\"\">&lt;v-icon type=\"link\"&gt;&lt;/v-icon&gt;</code></pre>\n\n    <p _v-73b20894=\"\">最终会渲染为：</p>\n\n    <pre _v-73b20894=\"\"><code class=\"lang-html\" _v-73b20894=\"\">&lt;i class=\"anticon anticon-link\"&gt;&lt;/i&gt;</code></pre>\n    <h2 _v-73b20894=\"\">本地部署</h2>\n    <p _v-73b20894=\"\">图标组件使用 <a href=\"http://www.iconfont.cn/\" target=\"blank\" _v-73b20894=\"\">iconfont.cn</a>，默认公网可访问。如需本地部署，可参考 <a href=\"https://github.com/ant-design/antd-init/tree/master/examples/local-iconfont\" target=\"blank\" _v-73b20894=\"\">示例</a>。</p>\n    <p style=\"color:#ff7800\" _v-73b20894=\"\">* 点击下面的图标即可复制标签</p>\n    \n    <api-table :content=\"content\" _v-73b20894=\"\">\n      <h3 _v-73b20894=\"\">Icon</h3>\n    </api-table>\n\n    <h2 _v-73b20894=\"\">图标列表</h2>\n    <h3 _v-73b20894=\"\">方向性图标</h3>\n\n    <ul class=\"anticons-list\" _v-73b20894=\"\">\n      <li class=\"icon-item\" v-for=\"icon in icons1Obj\" :class=\"{copied: Copied}\" :data-clipboard-text=\"_getCopyCode(icon.name)\" _v-73b20894=\"\">\n        <span class=\"anticon anticon-{{icon.name}}\" _v-73b20894=\"\"></span>\n        <span class=\"anticon-class\" _v-73b20894=\"\">{{icon.name}}</span>\n      </li>\n    </ul>\n\n    <h3 _v-73b20894=\"\">提示建议性图标</h3>\n\n    <ul class=\"anticons-list\" _v-73b20894=\"\">\n      <li class=\"icon-item\" v-for=\"icon in icons2Obj\" :class=\"{copied: Copied}\" :data-clipboard-text=\"_getCopyCode(icon.name)\" _v-73b20894=\"\">\n        <span class=\"anticon anticon-{{icon.name}}\" _v-73b20894=\"\"></span>\n        <span class=\"anticon-class\" _v-73b20894=\"\">{{icon.name}}</span>\n      </li>\n    </ul>\n\n    <h3 _v-73b20894=\"\">网站通用图标</h3>\n\n    <ul class=\"anticons-list\" _v-73b20894=\"\">\n      <li class=\"icon-item\" v-for=\"icon in icons3Obj\" :class=\"{copied: Copied}\" :data-clipboard-text=\"_getCopyCode(icon.name)\" _v-73b20894=\"\">\n        <span class=\"anticon anticon-{{icon.name}}\" _v-73b20894=\"\"></span>\n        <span class=\"anticon-class\" _v-73b20894=\"\">{{icon.name}}</span>\n      </li>\n    </ul>\n\n  </section>\n</div>\n\n";


